<template>
    <div class="container">
        <div class="header">
            <h1>四川疫情 - Echarts 数据可视化</h1>
            <div class="show-time">当前时间: &nbsp;{{ nowDate }}</div>
        </div>
        <div class="main">
            <div class="column">
                <div class="panel">
                    <h2>新增确诊-国家</h2>
                    <div class="chart" id="ChartLeft1"></div>
                    <div class="panel-footer"></div>
                </div>
                <div class="panel">
                    <h2>国外新增确诊趋势</h2>
                    <div class="chart" id="ChartLeft2"></div>
                    <div class="panel-footer"></div>
                </div>
                <div class="panel">
                    <h2>国内外累计治愈</h2>
                    <div class="chart" id="ChartLeft3"></div>
                    <div class="panel-footer"></div>
                </div>

            </div>
            <div class="column">
                <div class="num">
                    <div class="no-hd">
                        <ul>
                            <li>{{ addNum }}</li>
                            <li>{{ countNum }}</li>
                        </ul>
                    </div>
                    <div class="no-bd">
                        <ul>
                            <li>新增确诊</li>
                            <li>累计确诊</li>
                        </ul>
                    </div>

                </div>
                <!-- 地图模块 -->
                <div class="map">
                    <div class="map1"></div>
                    <div class="map2"></div>
                    <div class="map3"></div>
                    <div class="map-chart" id="mapChart"></div>
                </div>

            </div>
            <div class="column">
                <div class="panel">
                    <h2>四川11月数据统计</h2>
                    <div class="chart" id="ChartRight1"></div>
                    <div class="panel-footer"></div>
                </div>
                <div class="panel">
                    <h2>四川新增疫情与去年对比</h2>
                    <div class="chart" id="ChartRight2"></div>
                    <div class="panel-footer"></div>
                </div>
                <div class="panel">
                    <h2>四川11月疫情分布</h2>
                    <div class="chart" id="ChartRight3"></div>
                    <div class="panel-footer"></div>
                </div>

            </div>
        </div>
    </div>
</template>



<script>
import * as echarts from 'echarts';
export default {
    name: "HeaderView",
    created() {
        this.currentTime()
        if(localStorage.nums){
            let obj=JSON.parse(localStorage.nums)
            this.addNum=obj.addNum
            this.countNum=obj.countNum
        }
    },
    mounted() {
        //加载所有Echarts
        this.Load()
        this.setNumChange()
    },


    data() {
        return {
            nowDate: '',
            dataList: [],
            addNum: 70,
            countNum: 7802,
            timeRandom:2000,
        }
    },
    methods: {
        Load() {
            this.RenderEcharts1()
            this.RenderEcharts2()
            this.RenderEchartsLeft()
            this.RenderEchartsLeft3()

            this.RenderEchartsRight2()
            this.RenderEchartsRight3()
            this.RenderMaps()
            setInterval(()=>{
               this.timeRandom = parseInt(5000 * Math.random())
            },1000)
        },
        /**
         * 数量随机加减 
         */
        setNumChange() {
            let num;
            setInterval(() => {

                if(this.addNum>=158){

                    this.addNum=parseInt(158 * Math.random())

                }

                num = parseInt(5 * Math.random())
                num % 2 == 0 ? this.addNum += 3 : this.addNum = this.addNum += 2
                this.countNum += num

                let obj={
                    addNum:this.addNum,
                    countNum:this.countNum
                }
                localStorage.nums=JSON.stringify(obj)
            }, this.timeRandom)
            console.log(num)
        },

        /**
         * 获取当前时间
         */
        getDate() {
            let date = new Date();
            let year = date.getFullYear(); // 年
            let month = date.getMonth() + 1; // 月
            let day = date.getDate(); // 日
            let week = date.getDay(); // 星期
            let weekArr = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
            let hour = date.getHours(); // 时
            hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
            let minute = date.getMinutes(); // 分
            minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
            let second = date.getSeconds(); // 秒
            second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
            this.nowDate = `${year}年${month}月${day}日 ${hour}时${minute}分${second}秒 ${weekArr[week]}`;
        },
        /**
         * 定时器调取当前时间
         */
        currentTime() {
            setInterval(() => {
                this.getDate()
            }, 1000)
        },
        /**
         * 获取echarts dom节点
         * 左侧第一个柱状图
         */
        RenderEcharts1() {
            //初始化实例对象 
            let myChart = echarts.init(document.getElementById('ChartLeft1'));
            //配置项
            let option = {
                // color:['#2f89cf'], 
                //柱状图颜色
                color: ['#2f89cf'],
                // 鼠标触摸 提示框配置
                tooltip: {
                    // trigger 触发方式
                    trigger: 'axis',
                    axisPointer: {
                        //type 默认为line 可选择shadow
                        type: "shadow"
                    }
                },
                // X轴  相关配置
                /**
                 *  ↑
                 *  ↑   ↑ y轴
                 *  ↑
                 *  ↑
                 *  ↑       → x轴
                 *  → → → → → → → → →
                 */
                xAxis: {
                    type: 'category',
                    //修改刻度标签相关样式   也就是柱状图 X 轴下面的文字刻度的样式
                    axisLabel: {
                        color: 'rgba(255,255,255,.6)',
                        fontSize: '0.175rem',
                    },
                    //X轴线  配置项
                    axisLine: {
                        //是否展示轴线
                        show: true,
                        lineStyle: {
                            // color:'red'
                        }
                    },
                    //data X轴显示 数据
                    data: ['意大利', '日本', '韩国', '法国', '巴西', '德国', '英国'],
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        color: 'rgba(255,255,255,.6)',
                        fontSize: '0.175rem',
                    },
                    //Y轴线  配置项
                    axisLine: {
                        //是否展示轴线
                        show: true,
                        //轴线的Style样式
                        lineStyle: {
                            // Y轴 线的颜色
                            color: 'rgba(255,255,255,.6)',
                            // Y轴 线的宽度
                            width: 1,
                        }
                    },
                    // Y 轴分割线的配置
                    splitLine: {
                        lineStyle: {
                            // Y 轴分割线的线条颜色
                            color: 'rgba(255,255,255,.2)',
                        }
                    }
                },
                // 柱状图位置   图标大小
                grid: {
                    left: '4%',
                    top: '10px',
                    bottom: '0%',
                    right: '4%',
                    containLabel: true,
                },
                //柱子的配置项
                series: [
                    {
                        type: 'bar',
                        //barWidth  ---->修改柱子的宽度
                        barWidth: "45%",
                        //itemStyle  ------------>柱子的样式
                        itemStyle: {
                            // ------------>柱子的圆角
                            barBorderRadius: 5,

                        },
                        //Y 轴数据
                        data: [208635, 102524, 89562, 78589, 65874, 32556, 10052],
                    }
                ],
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },

        RenderEchartsLeft() {
            let myChart = echarts.init(document.getElementById('ChartLeft2'));
            let option = {
                color: '#EE6666',
                tooltip: {
                    trigger: 'none',
                    axisPointer: {
                        type: 'cross'
                    }
                },
                // 线图位置   图标大小
                grid: {
                    left: '4%',
                    top: '10px',
                    bottom: '0%',
                    right: '4%',
                    containLabel: true,
                },
                xAxis: [
                    {
                        type: 'category',
                        axisTick: {
                            alignWithLabel: true,

                        },
                        axisLine: {
                            onZero: false,
                            lineStyle: {
                                color: 'rgba(255,255,255,.2)'
                            }
                        },
                        axisPointer: {
                            label: {
                                formatter: function (params) {
                                    return (

                                        '新增病例  ' +
                                        params.value +
                                        (params.seriesData.length ? '：' + params.seriesData[0].data : '')
                                    );
                                }
                            }
                        },
                        // 可以给特殊的那条数据设置单独样式  textStyle 文字样式
                        data: ['2022-1', '2022-2', '2022-3', '2022-4', '2022-5', '2022-6', '2022-7', '2022-8', '2022-9', '2022-10', { value: '2022-11', textStyle: { color: 'red' } }, '2022-12']
                    }
                ],
                yAxis: [
                    {
                        type: 'value',

                        axisLabel: {
                            color: 'rgba(255,255,255,.6)',
                            fontSize: '0.175rem',
                        },
                        // Y 轴分割线的配置
                        splitLine: {
                            lineStyle: {
                                // Y 轴分割线的线条颜色
                                color: 'rgba(255,255,255,.2)',
                            }
                        }
                    }
                ],
                series: [
                    {
                        name: '222',
                        type: 'line',
                        smooth: true,
                        emphasis: {
                            focus: 'series'
                        },
                        data: [
                            50425, 18098, 7342, 24669, 35890, 17816, 87884, 52816, 49656, 9837, 4480, 28794,
                        ]
                    }
                ]
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },

        RenderEchartsLeft3() {
            let myChart = echarts.init(document.getElementById('ChartLeft3'));
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: '#000',
                            textBorderWidth: 3,
                        }
                    }
                },
                legend: {
                    data: ['国内', '国外'],
                    textStyle: {
                        color: '#fff'
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                        type: 'category',
                        boundaryGap: false,
                        data: ['2022-1', '2022-2', '2022-3', '2022-4', '2022-5', '2022-6', '2022-7', '2022-8', '2022-9', '2022-10', { value: '2022-11', textStyle: { color: 'red' } }, '2022-12']
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        axisLabel: {
                            color: 'rgba(255,255,255,.6)',
                            fontSize: '0.175rem',
                        },
                        // Y 轴分割线的配置
                        splitLine: {
                            lineStyle: {
                                // Y 轴分割线的线条颜色
                                color: 'rgba(255,255,255,.2)',
                            }
                        }
                    }
                ],
                series: [
                    {
                        name: '国内',
                        type: 'line',
                        stack: 'Total',
                        areaStyle: {},
                        emphasis: {
                            focus: 'series'
                        },
                        data: [50, 55, 88, 155, 5, 66, 134, 156, 221, 33, 5, 111,]
                    },
                    {
                        name: '国外',
                        type: 'line',
                        stack: 'Total',
                        areaStyle: {},
                        emphasis: {
                            focus: 'series'
                        },
                        data: [18, 7, 8, 18, 19, 12, 6, 20, 12, 16, 21, 28,]
                    },

                ]
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },


        RenderEchartsRight2() {
            let myChart = echarts.init(document.getElementById('ChartRight2'));
            let option = {
                tooltip: {
                    trigger: 'axis'
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    top: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                //是否下载当前视图为图片
                // toolbox: {
                //     feature: {
                //         saveAsImage: {}
                //     }
                // },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月']
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        color: 'rgba(255,255,255,.6)',
                        fontSize: '0.175rem',
                    },
                    // Y 轴分割线的配置
                    splitLine: {
                        lineStyle: {
                            // Y 轴分割线的线条颜色
                            color: 'rgba(255,255,255,.2)',
                        }
                    }

                },
                series: [
                    {
                        name: '本土新增',
                        type: 'line',
                        stack: 'Total',
                        data: [120, 132, 101, 134, 90, 230, 210, 120, 132, 101, 134, 90]
                    },
                    {
                        name: '本土确诊',
                        type: 'line',
                        stack: 'Total',
                        data: [220, 182, 191, 234, 290, 330, 310, 90, 230, 210, 120, 132,]
                    },
                ]
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },

        RenderEchartsRight3() {
            let myChart = echarts.init(document.getElementById('ChartRight3'));
            let myColor = ["#a92259", "#1089E7", "#F57474", "#56D0E3", "#F8B448", "#16a065", "#8B78F6", "#ff7edb",]

            let option = {
                grid: {
                    left: '3%',
                    right: '4%',
                    top: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                // 
                tooltip: {
                    show: true,

                },
                series: [
                    {
                        name: '四川11月疫情分布',
                        type: 'pie',
                        radius: [20, 100],
                        center: ['50%', '50%'],
                        roseType: 'area',
                        //是否按顺时针 逆时针展示
                        clockwise: true,
                        // 横向柱子的样式配置
                        itemStyle: {
                            borderRadius: 3, //饼图的圆角
                            // ------------>饼图的颜色方法  
                            color: (params) => {
                                // params 是也是每个饼图的实例对象  这里面会拿到每个饼图
                                let num = myColor.length;
                                // dataIndex 是每个饼图的索引
                                return myColor[params.dataIndex % num]
                            },
                        },
                        data: [
                            { value: 1026, name: '成都' },
                            { value: 587, name: '眉山' },
                            { value: 825, name: '乐山' },
                            { value: 665, name: '资阳' },
                            { value: 542, name: '广安' },
                            { value: 451, name: '遂宁' },
                            { value: 425, name: '金堂' },
                            { value: 352, name: '泸州' },
                        ]
                    }
                ]
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },




        /**
         * 获取echarts dom节点
         * 右侧第一个柱状图
         */
        RenderEcharts2() {
            //实例化对象
            let myChart = echarts.init(document.getElementById('ChartRight1'));
            // 柱子颜色数组
            let myColor = ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6", "#ff7edb"]
            //指定配置数据
            let option = {
                // grid 图表位置 大小
                grid: {
                    left: '0%',
                    top: '10px',
                    bottom: '0%',
                    right: '4%',
                    containLabel: true,
                },
                //X轴 配置项
                xAxis: {
                    // 是否展示X轴
                    show: false,
                },
                //Y轴 配置项  可以是数组   对应我们 series里面的数据
                yAxis: [
                    //第一组 Y 轴的配置  左
                    {
                        type: 'category',
                        // 是否反转 Y轴数据
                        inverse: true,
                        // 刻度标签 配置项
                        axisLabel: {
                            // 设置 Y 轴文字颜色
                            color: '#fff',
                        },
                        axisLine: {
                            //是否显示 Y轴轴线  
                            show: false,
                        },
                        axisTick: {
                            //是否显示 Y轴刻度 
                            show: false,
                        },
                        data: ['新增本土', '新增本土无症状', '新增境外', '新增确诊', '现有确诊', '累计确诊']
                    },
                    //第二组 Y 轴的配置  右  
                    {
                        type: 'category',

                        // 是否反转 Y轴数据
                        inverse: true,
                        // 刻度标签 配置项
                        axisLabel: {
                            // 设置 Y 轴文字颜色
                            color: '#fff',
                        },
                        axisLine: {
                            //是否显示 Y轴轴线  
                            show: false,
                        },
                        axisTick: {
                            //是否显示 Y轴刻度 
                            show: false,
                        },
                        data: [500, 600, 778, 541, 256, 389]
                    }
                ],
                series: [
                    {
                        name: '条',
                        type: 'bar',
                        data: [500, 600, 778, 541, 256, 389],
                        // 横向柱子的样式配置
                        itemStyle: {
                            // ------------>柱子的圆角
                            barBorderRadius: 20,
                            // ------------>柱子的颜色方法  
                            color: (params) => {
                                // params 是每个柱子的实例对象  这里面会拿到每个柱子
                                let num = myColor.length;
                                // dataIndex 是每个柱子的索引
                                return myColor[params.dataIndex % num]
                            },
                        },
                        //----------->柱子间接的距离
                        barCategoryGap: 60,

                        //----------->柱子的宽度
                        barWidth: 15,
                        //设置 横向柱子内文字显示配置
                        label: {
                            show: true,
                            position: 'inside',
                            /**
                             * {c} 会自动解析 data里面的数据 自动转换
                             */
                            formatter: "{c}%"
                        },
                        // 设置 柱子的层级 类似于Css的 z-index  这里设置0 第二组 "框" 的数据设置为 1 使其能压在柱子1 上 
                        yAxisIndex: 0,
                    },
                    // 原本是第二根横向柱状图  这里我要改他的样式  改成一个边框的样子
                    {
                        name: '框',
                        type: 'bar',
                        data: [1000, 1000, 1000, 1000, 1000, 1000],
                        /**
                         * 把 背景色设置为 none
                         * 设置一个边框颜色
                         * 设置边框宽度 和 边框的圆角
                         */
                        itemStyle: {
                            color: 'none',
                            borderColor: '#00c1de',
                            borderWidth: 3,
                            barBorderRadius: 17,
                        },
                        //----------->柱子间接的距离
                        barCategoryGap: 60,

                        //----------->柱子的宽度
                        barWidth: 15,

                        // 设置 柱子的层级 类似于Css的 z-index  这里设置1 第二组 "框"  使其能position
                        yAxisIndex: 1,
                    },
                ]
            };
            //把配置项给实列对象
            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })
        },





        RenderMaps() {
            let myChart = echarts.init(document.getElementById('mapChart'));
            window.dataList = [
                { name: '澳门', value: 18 },
                { name: '香港', value: 5 },
