                { name: '江西', value: 936 },
                { name: '福建', value: 307 },
                { name: '安徽', value: 990 },
                { name: '浙江', value: 16 },
                { name: '江苏', value: 633 },
                { name: '上海', value: 394 },
                { name: '黑龙江', value: 484 },
                { name: '吉林', value: 93 },
                { name: '辽宁', value: 8 },
                { name: '内蒙古', value: 75 },
                { name: '山西', value: 133 },
                { name: '河北', value: 319 },
                { name: '天津', value: 137 },
                { name: '北京', value: 21 },
                { name: '南海诸岛', value: 21 },
            ];
            let option = {
                tooltip: {
                    triggerOn: "click",
                    formatter: function (e, t, n) {
                        return '.5' == e.value ? e.name + "：有疑似病例" : e.seriesName + "<br />" + e.name + ":" + e.value + "例"
                    }
                },
                toolbox: {
                    show: true,
                    orient: 'vertical',
                    left: 'right',
                    top: 'center',
                    feature: {
                        dataView: { readOnly: false },
                        restore: {},
                        saveAsImage: {}
                    }
                }, // 提供下载工具
                visualMap: {
                    min: 0,
                    max: 100000,
                    left: 26,
                    bottom: 40,
                    textStyle: {
                        fontSize: '.175rem',//字体大小
                        color: '#ffffff'//字体颜色
                    },
                    pieces: [{
                        gt: 5000,
                        label: "> 5000人",
                        color: "rgba(235, 28, 39,.7)"
                    }, {
                        gte: 1000,
                        lte: 5000,
                        label: "1000 - 5000人",
                        color: "rgba(255, 96, 144,.6"
                    }, {
                        gte: 100,
                        lt: 1000,
                        label: "100 - 1000人",
                        color: "rgba(139, 120, 246.7)"
                    }, {
                        gt: 10,
                        lt: 100,
                        label: "10 - 100人",
                        color: "rgba(86, 208, 227,.7)"
                    }, {
                        gt: 1,
                        lt: 10,
                        label: "1 - 10人",
                        color: "rgba(255, 255, 255,.6)"
                    }],
                    show: true,

                },
                geo: {
                    map: "china",
                    roam: !1,
                    scaleLimit: {
                        min: 1,
                        max: 2
                    },
                    zoom: 1.2,
                    top: 120,
                    label: {
                        normal: {
                            show: !0,
                            fontSize: "0.165rem",
                            color: "#fff"
                        }
                    },
                    itemStyle: {
                        normal: {
                            areaColor: 'rgba(128, 128, 128, 0)', //rgba设置透明度0
                            borderColor: '#000e4e',//省份边框颜色
                            borderWidth: 0.4,//省份边框
                        },
                        emphasis: {
                            areaColor: "#fff",
                            shadowOffsetX: 0,
                            shadowOffsetY: 0,
                            borderWidth: 0
                        }
                    },
                },
                series: [{
                    name: "疑似确诊病例",
                    type: "map",
                    geoIndex: 0,
                    itemStyle: {
                        // ------------>柱子的圆角
                        barBorderRadius: 20,
                        opacity: '0.2',
                        // ------------>柱子的颜色方法  

                    },
                    data: window.dataList
                }]
            };

            myChart.setOption(option);

            //让图标跟随屏幕自适应
            window.addEventListener('resize', () => {
                myChart.resize();
            })

        },





    }
}
</script>

